import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniLoadMore
	},
	data(){
		return{
			goodsId:'',
			merchId:'',
			url:'',
			active:1,
			rangeActive:'',
			sh:'',
			dataList:[],
			page:1,
			pageSize:5,
			total:0,
			more:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   onLoad(e){
		 this.merchId=e.cateId
		 this.goodsId=e.goodsId
		 },
	mounted(){
		var that=this
		this.url='&r=api.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+this.active
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
			this.getList(this.page)
		}
		setTimeout(function(){
			that.$getHeight('#sv',(res) =>{
				that.sh=res
			})
		},0)
	},
	methods:{
		to(w,id){
			uni.navigateTo({
				url:`/pages/index/${w}?id=${id}&merchId=${this.merchId}`
			})
		},
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page)
		},
		toggleRange(t){
			this.rangeActive=t
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
			if(this.active!=3){
				this.rangeActive=''
				this.url='&r=api.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+this.active
			}else{
				this.rangeActive= this.rangeActive == 1 ? 2:1
				if(this.rangeActive==1){
					this.url='&r=api.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+3
				}else if(this.rangeActive==2){
					this.url='&r=api.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+4
				}
			}
		},
		getList(p){
			var that=this
			// var params={
			//   page:p,
			//   pagesize: this.pageSize
			// }
			if(this.page==1){
				this.$loading()
			}
			  // var url='/wangtosale_list'
			  this.$apiPost(this.url).then((res) =>{
				  that.total=res.total
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
		addCollect(id){
			var that=this
			var params={
			  uid:this.uid,
			  token: this.token,
				goodsid:id
			}
			  var url='&r=api.member.cart.add'
			  this.$apiPost(url,params).then((res) =>{
					// that.options[2].info++
									that.$msg('添加成功')
			  })
		},
	}
}
